.cart-view {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.empty-cart {
  margin-top: 3rem;
  text-align: center;
  font-weight: 500;
  font-size: var(--font-size-larger);
}

.cart-items-list {
  max-width: 45vw;
}

@media (max-width: 1100px) {
  .cart-items-list {
    max-width: 80vw;
  }
}

.cart-total-savings,
.cart-total-price {
  margin: 1rem 0;
}

.cart-total-savings span,
.cart-total-price span {
  margin-left: 10px;
  font-weight: 500;
  font-size: var(--font-size-larger);
}


.the-end {
  margin-top: 2rem;
}



