.login-form {
  -ms-flex: none;
      flex: none;
  width: 250px;
  border-radius: 2px;
  background-color: rgba(115, 56, 139, 0.45);
  -webkit-box-shadow: 0 4px 10px 3px rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 10px 3px rgba(0, 0, 0, 0.2);
}

.login-form label {
  display: none;
}

.login-form input {
  border-color: #FFF;
}

.login-button {
  font-family: var(--sans-serif-font);
}

.login-form fieldset + fieldset {
  text-align: center;
}


/* component transitions */
.logintrans-appear {
  opacity: 0.01;
  -webkit-transition-delay: 0.4s;
       -o-transition-delay: 0.4s;
          transition-delay: 0.4s;
  -webkit-transition-duration: 0.4s;
       -o-transition-duration: 0.4s;
          transition-duration: 0.4s;
  -webkit-transform: translate3d(0, 0, -100vh);
          transform: translate3d(0, 0, -100vh);
}

.logintrans-appear.logintrans-appear-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}


.logintrans-enter {
  opacity: 0.01;
  -webkit-transform: translate3d(0, 0, -100vh);
          transform: translate3d(0, 0, -100vh);
}

.logintrans-enter.logintrans-enter-active {
  opacity: 1;
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}


.logintrans-leave {
  opacity: 1;
}

.logintrans-leave.logintrans-leave-active {
  opacity: 0.01;
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
}



